import React, { Fragment } from "react";
import MetaTags from "react-meta-tags";
import LayoutThree from "../../layouts/LayoutThree";
import FeatureIconTwo from "../../wrappers/feature-icon/FeatureIconTwo";
import BlogFeatured from "../../wrappers/blog-featured/BlogFeatured";
import HeroSliderOne from "../../wrappers/hero-slider/HeroSliderOne";
import NewProductGrid from "../../wrappers/product/NewProductGrid";
import TabProductTwelve from "../../wrappers/product/TabProductTwelve";
import BannerThirty from "../../wrappers/banner/BannerThirty";
import BannerOne from "../../wrappers/banner/BannerOne";
import TabProduct from "../../wrappers/product/TabProduct";
const HomeFashionThree = () => {
  return (
    <Fragment>
      <MetaTags>
        <title>Impetrosys</title>
        <meta
          name="description"
          content="Fashion"
        />
      </MetaTags>
      <LayoutThree
        headerContainerClass="container-fluid"
        headerPaddingClass="header-padding-2"
        headerTop="visible"
      >
        {/* hero slider */}
        <HeroSliderOne />
        {/* feature icon */}
        {/* <BannerEleven spaceBottomClass="pb-50" /> */}
        <BannerOne spaceTopClass="pt-60" spaceBottomClass="pb-30" />
       
        {/* product grid */}
        {/* <TabProduct spaceBottomClass="pb-60" category="fashion" /> */}
        <NewProductGrid category="fashion" limit={8} />
          {/* Banner */}
          <BannerThirty spaceTopClass="pt-0" spaceBottomClass="pb-0" />
        {/* blog featured */}          
        {/* <BlogFeatured spaceBottomClass="pb-55" /> */}
        {/* best  */}
        <TabProductTwelve
          category="kids"
          spaceTopClass="pt-10"
          spaceBottomClass="pb-10"
          sectionTitle="Best Products"
        />
         <BlogFeatured spaceTopClass="pt-30" spaceBottomClass="pb-20" />
         <FeatureIconTwo spaceTopClass="pt-0" spaceBottomClass="pb-0" />

      </LayoutThree>
    </Fragment>
  );
};

export default HomeFashionThree;
